import Scroll from 'base/scroll/scroll'
import { changeAvatar, changeNickname } from 'api/api'
import Underline from 'base/hr/underline'
import Prompt from 'base/prompt/prompt'
import { mapGetters, mapActions} from 'vuex'

export default {
  data() {
    return {

    }
  },
  created() {
  },
  computed: {
    ...mapGetters([
      'userAccount'
    ])
  },
  methods: {
    back() {
      this.$router.back()
    },
    changeNickNmae() {
      this.$refs.prompt.show()
    },
    onFileChange(e) {
      let files = e.target.files || e.dataTransfer.files
      if (!files.length) return
      let token = this.userAccount.token
      let uid = this.userAccount.uid
      let avatar = files[0]
      // 组成formdata上传头像
      let body = new FormData()
      body.append('token', token)
      body.append('uid', uid)
      body.append('avatar', avatar)
      changeAvatar(body).then((res)=>{
        if(res.status === 1) {
          let userAccount = this.userAccount
          let newUserAccount = JSON.parse(JSON.stringify(userAccount))  
          newUserAccount.avatar = res.errorMsg
          this.changeUserAccount(newUserAccount)
        }
      })
    },
    success(value) {
      // console.log(value)
      let token = this.userAccount.token
      let uid = this.userAccount.uid
      let nickname = value
      changeNickname(token,uid,nickname).then((res)=>{
        if(res.status === 1) {
          // 这里重新组装新的user，统一发起actions请求，修改store状态树
          let userAccount = this.userAccount
          let newUserAccount = JSON.parse(JSON.stringify(userAccount))  
          newUserAccount.nickname = res.errorMsg
          this.changeUserAccount(newUserAccount)
        }
      })
    },
    cancel() {
      // 清楚登陆状态，并且返回me页面
      this.clearUserAccount()
      this.$router.back()
    },
    ...mapActions([
      'clearUserAccount',
      'changeUserAccount'
    ])
  },
  components: {
    Scroll,
    Underline,
    Prompt
  }
}



