import Vue from 'vue'
export default {
  props: {
    text: {
      type: String,
      default: ''
    }
  },
  data () {
    return {
      showFlag: false,
      title: '换一个好的昵称',
      content: '',
      placeholder: '',
      value: '',
      style: {},
      yes: {
          text: '决定了',
          style: {}
      },
      no: {
          text: '改变想法了',
          style: {
            color: 'red'
          }
      }
    }
  },
  created() {
    this.$watch('value', (newValue) => {
      this.$emit('value', newValue)
    })
  },
  methods: {
    show() {
      this.showFlag = true
    },
    hide() {
      this.showFlag = false
    },
    success () {
      this.hide()
      this.$emit('confirm',this.value)
    },
    cancel () {
      this.hide()
      this.$emit('cancel')
    }
  }
}



