export default {
  data() {
    return {
      stylesheet:`height:${this.height}px;color:${this.color}`
    }
  },
  props: {
    height: {
      type: Number,
      default: 5
    },
    color:{
      type: String,
      default: '#dcdcdc'
    }
  }
}



